<template>
	<div class="font-settings" :class="{ '_opened': isOpened }">
		<div class="font-settings__icon" title="Размер шрифта">
			<icon icon="heading" />
		</div>
		<div class="font-settings__icon" title="Межстрочный интервал">
			<icon icon="text-height" />
		</div>
		<div class="font-settings__button" @click="changeFontSize(1)">
			<icon icon="plus-circle" />
		</div>
		<div class="font-settings__button" @click="changeLineHeight(0.1)">
			<icon icon="plus-circle" />
		</div>
		<div class="font-settings__button" @click="changeFontSize(-1)">
			<icon icon="minus-circle" />
		</div>
		<div class="font-settings__button" @click="changeLineHeight(-0.1)">
			<icon icon="minus-circle" />
		</div>
	</div>
</template>

<script>
	export default {
		name: "FontSettings",

		data() {
			return {
				isOpened: false,
				fontSize: 18,
				lineHeight: 1.42,
			}
		},

		methods: {
			toggleVisible() {
				this.isOpened = !this.isOpened;
			},
			changeFontSize(value) {
				const newFontSize = this.fontSize + value;

				if (newFontSize > 12 && newFontSize < 32) {
					this.fontSize = newFontSize;
				}

				this.$emit('change-font', { fontSize: this.fontSize, lineHeight: this.lineHeight });
			},
			changeLineHeight(value) {
				let newLineHeight = this.lineHeight + value;
				newLineHeight = +newLineHeight.toFixed(2);

				if (newLineHeight > 1 && newLineHeight < 3) {
					this.lineHeight = newLineHeight;
				}

				this.$emit('change-font', { fontSize: this.fontSize, lineHeight: this.lineHeight });
			},
		},

		created() {
			app.$fontSettings = this;
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.font-settings {
		display: grid;
		grid-template-columns: 50% 50%;
		flex-direction: column;
		position: absolute;
		opacity: 0;
		right: 0;
		bottom: 120px;
		z-index: 1;
		height: 100px;
		width: 72px;
		transition: opacity .3s ease-in-out;
		padding: 15px;
		margin: 0 4px;

		&._opened {
			opacity: 1;
		}

		&__icon, &__button {
			text-align: center;
			font-size: 16px;
		}

		&__icon {
			color: @color-beige;
		}

		&__button {
			cursor: pointer;

			&:hover {
				color: @color-pink;
			}
		}

		.close {
			position: absolute;
			top: 10px;
			right: 10px;
			font-size: 14px;
			cursor: pointer;

			&:hover {
				color: @color-pink;
			}
		}
	}
</style>


